@import 'fonts';
@import 'colors';

@mixin centered {
	display: flex;
	align-items: center;
	justify-content: center; }

@mixin absolute {
	position: absolute;
	top: 0;
	left: 0; }

@mixin layer {
	@include absolute;
	width: 100%;
	height: 100%; }

@mixin column {
	display: flex;
	flex-direction: column; }

.rr {
	flex-direction: row-reverse; }

html {
	font-size: 62.5%; }
body {
	@include f-lato; }

.container {
	max-width: 111rem; }

.ghost-link {
	display: block;
	cursor: pointer;
	position: absolute;
	width: 100%;
	height: 100%;
	z-index: 100; }

.link {
	@include layer;
	@include centered;
	&__layer {
		@include layer;
		z-index: 1;
		transition: 0.4s;
		opacity: 0.8; }
	&__logo {
		z-index: 2; }
	&:hover &__layer {
		background-color: $green; } }


@import 'sections/header';
@import 'sections/products';

@import 'blocks/card';
