.header {
	height: 100vh;
	width: 100%;
	position: relative;
	&__row {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 3.5rem;
		z-index: 50;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%; }
	&__body {
		width: 100%;
		height: 100%; } }


.nav {
	display: flex;
	justify-content: center;
	align-items: center;
	display: flex;
	&__item {
		color: $black-3;
		font-size: 1.3rem;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 0.26rem;
		margin-right: 7.5rem;
		&:last-child {
			margin-right: 0; }
		&--logo {} } }

